import React, { Component } from "react";
import "./CheckListItems.scss";
import Accordion from 'react-bootstrap/Accordion';
import Card from 'react-bootstrap/Card';
import { setActiveRecommandation, addRecommendedProduct } from 'modules/my-registry/MyRegistryActions';
import { connect} from 'react-redux';
import { GET_RECOMMENDATIONS } from 'api/queries';
import { getRecommendation } from 'api/axios';

class CheckListItems extends Component {

    constructor(props) {
        super(props);
        // create a ref to store the checkList header DOM element
        this.checkListHeader = React.createRef();
    }

    changeActiveRecommendation = (category) => {
        this.props.dispatch(setActiveRecommandation(category))
        const recommededProductCount = 3
        let data = {
            query: GET_RECOMMENDATIONS,
            variables: {
                productCount: recommededProductCount,
                productType: "product_type:" + category
            }
        }
        getRecommendation({ params: data }).then(res => {
            data = res.data.data.products.edges.map(item => {
                let resData = { ...item.node }
                resData['quantity'] = 1
                return resData
            })
            this.props.dispatch(addRecommendedProduct(data))
        }).catch(err => {
            console.error(err.message)
        })
    }

    getSubList = (getSubList) => {
        let subItems = getSubList.map(({ id, categoryName, addedItemsCount, totalItemsCount }) => {
            return (
                <div className="d-flex card-body-contain py-2" key={categoryName + id} onClick={() => this.changeActiveRecommendation(categoryName)}>
                    <p className="mb-0 card-body-header body-text">{categoryName}</p>
                    <div className='ml-auto mb-0 align-self-end card-progress'>
                        {(addedItemsCount >= totalItemsCount) ? <div className="icon-done"></div> : <div></div>}
                    </div>
                </div>
            )
        });
        return subItems;
    }

    handleToggleEvent = () => {
        const checkListIconEl = this.checkListHeader.current.firstChild;
        checkListIconEl.classList.toggle("closed");
        checkListIconEl.classList.toggle("opened");
    }

    render() {

        let subListItem = this.getSubList(this.props.item.subCategories)
        // TODO: For calculation for subitem
        // let parentTotal = 10;
        // let parentAdded = 16;

        // for (let subItems of this.props.item.subCategories) {
        //     if (subItems.added !== subItems.total) {
        //         parentAdded += Number(subItems.added);
        //         parentTotal += Number(subItems.total);
        //     }
        // }

        return (
            <Accordion className="accordion-list mb-5" key={this.props.item.categoryName}>
                <Card>
                    <Accordion.Toggle
                        onClick={this.handleToggleEvent}
                        as={Card.Header}
                        id={this.props.item.categoryName}
                        eventKey={this.props.item.categoryName}
                    >
                        <div ref={this.checkListHeader} className='d-flex icon-checklist'>
                            <span className="mr-3 checklist-icon-arrow closed"></span>
                            <p className="mb-0 body-text">{this.props.item.categoryName}</p>
                            <div className='ml-auto mb-auto align-self-end card-progress'>
                                {this.props.item.addedItemsCount}/{this.props.item.totalItemsCount}
                            </div>
                        </div>
                    </Accordion.Toggle>
                    <Accordion.Collapse className='mt-1 ml-6' eventKey={this.props.item.categoryName}>
                        <Card.Body className="py-1">
                            <div>{subListItem}</div>
                        </Card.Body>
                    </Accordion.Collapse>
                </Card>
            </Accordion>
        );

    }
}

export default connect()(CheckListItems);